import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import _ from "lodash";

import ModalService from "../../../../../flux/utils/OraModalService";
import {clearCartAndRedirect} from "../../../actions/cart"
import {redirectOnCustomerSearch} from "../../../../fix/actions/data";
import {loyaltyDuration} from "../../Utils";
import FixDetailsModal from "../fix/productDetails/FixDetailsModal";
import OnlineUtils from "../../../../../utils/OnlineUtils"
import PriceRow from "../fix/PriceRow";
import PriceBox from "../fix/PriceBox";
import ProductRow from "../fix/ProductRow";
import FullProductRow from "../fix/FullProductRow";

class CommonPropositionEntry extends Component {

    constructor(props) {
        super(props);
        this.props.redirectOnCustomerSearch();
        this.name = this.props.bundleNo + "_" + this.props.entryNo;
        this.actionPrefix = 'productDetails-main';
    }

    componentDidMount() {
        OPL.UI.loadModules(document.getElementById(`common-proposition-entry-${this.name}`), jsModules());
    }

    componentDidUpdate() {
        OPL.UI.fire(OPL.UI.EVENTS.modules.layoutFixer.recalculate, `common-proposition-entry-${this.name}`);
        this.props.subEntries && this.props.subEntries.forEach(subEntry => {
            subEntry && ModalService.add((data) => {
                return (
                    <FixDetailsModal id={`${this.actionPrefix}-${subEntry.code}`}
                                     header={"SzczegÃ³Å‚y urzÄ…dzenia"}
                                     details={subEntry.details}
                                     icon={subEntry.thumbnailIcon}
                                     productName={subEntry.name}
                                     net={this.props.showNetPrices}
                    />)
            });
        })
    }

    changeProduct(bundleNo) {
        this.props.clearCartAndRedirect(bundleNo, OnlineUtils.getLastViewedOfferPage());
    }

    getChangeDescriptionForProduct = (product, descriptions) => {
        if (!!product.deviceType) {
            return descriptions.changeDeviceLabel;
        } else {
            return descriptions.detailsLabel;
        }
    };

    renderRejectionReasons(){
        if(this.props.rejectionReason && this.props.rejectionReason.length > 0){
            return (
                <div className="l-row u-padding-l u-padding-top-l u-padding-left-s u-border-top">
                    <div className="opl-msg opl-msg--box opl-msg--error">
                        <div className="o-icon-list">
                            <div className="o-icon-list__item">
                                <div className="o-icon-list__icon u-vertical-middle">
                                    <div aria-hidden="true" className="g-icon g-icon--error g-icon--before g-icon--s"></div>
                                </div>
                                <div className="o-icon-list__text u-vertical-middle">
                                    <p>{this.props.rejectionReason.map( (reason,i) =>{
                                        return (<b>{reason}<br/></b>);
                                        }
                                    )}
                                    <b>UsuÅ„ produkt z koszyka.</b>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        else{
            return null
        }
    }

    render() {
        const entriesLen = this.props.mainEntries.length;

        return (
            <div id={`common-proposition-entry-${this.name}`} className="g-white1-bg u-box-shadow u-spacing">
                <div className="u-position-relative">
                    <div className="u-padding-left-l-xl u-padding-right-l-xl">
                        <div className="l-row">
                            <div className="u-medium-hide u-large-hide u-padding-left-s u-padding-right-s u-padding-top-l u-padding-m">
                                <a href="#" onClick={() => this.props.onRemove()}
                                   className="opl-checkout__item__remove"/>
                                <PriceBox price={this.props.offerPrice} net={this.props.showNetPrices}/>
                            </div>
                            <div className="l-col l-col-8 l-col-small-12">
                                <div className="u-animate-height js-layout-fixer-group-1">
                                    <div className="js-height-sensitive-element">
                                        {
                                            /*No bottom border for last entry*/
                                            this.props.mainEntries.map((entry, i) => {
                                                return <ProductRow key={`main-entry-${i}`}
                                                                   product={entry}
                                                                   borderBottom={entriesLen !== i + 1}
                                                                   installationAddress={this.props.descriptions.installationAddress}
                                                                   potsNumber={this.props.descriptions.potsNumber}/>
                                            })
                                        }
                                    </div>
                                </div>
                            </div>
                            <div className="l-col l-col-4 u-small-hide">
                                <PriceRow id={`price-row-${this.name}`} price={this.props.offerPrice} net={this.props.showNetPrices}/>
                            </div>
                        </div>
                        {this.renderRejectionReasons()}
                        {
                            this.props.subEntries.map((subEntry, i) => {
                                return <FullProductRow key={`sub-entry-${i}`}
                                                       product={subEntry}
                                                       detailsLabel={this.props.descriptions.detailsLabel}
                                                       forFreeLabel={this.props.descriptions.forFreeLabel}
                                                       onChange={() => this.props.onChange({selectedSection: subEntry.section})}
                                                       onDetails={() => ModalService.open(`${this.actionPrefix}-${subEntry.code}`)}
                                                       changeLabel={this.getChangeDescriptionForProduct(subEntry, this.props.descriptions)}
                                                       net={this.props.showNetPrices}
                                                       onRemove={() => this.props.onVasRemove(subEntry.code)}
                                                       serviceFreeLabel={this.props.descriptions.serviceFreeLabel}

                                />
                            })
                        }
                        <div className="l-row u-padding-l u-padding-top-l u-padding-left-s u-border-top">
                            <span className="u-font-bold">
                                {loyaltyDuration(this.props.loyaltyLength)}
                            </span>
                        </div>
                        {(this.props.showButtons) ? <div className="l-row u-border-top">
                            <div className="l-col l-col--auto l-col-small-6 u-spacing-right-l u-padding-l u-padding-top-l u-padding-left-s u-small-border-right u-small-no-spacing-r u-small-text-center">
                                <a href="#" className="u-spacing-right-l u-small-no-spacing"
                                   onClick={() => this.changeProduct(this.props.bundleNo)}>
                                    {this.props.descriptions.changeLabel}
                                </a>
                            </div>
                            <div className="l-col l-col--auto l-col-small-6 u-spacing-right-l u-padding-l u-padding-top-l u-padding-left-s u-small-no-spacing-r u-small-text-center">
                                { this.props.overrideDefaultDetailsPartBy ? this.props.overrideDefaultDetailsPartBy :
                                    <a href="#" className="u-spacing-right-l u-small-no-spacing"
                                       onClick={() => this.props.onDetails()}>
                                        {this.props.descriptions.detailsLabel}
                                    </a>
                                }
                            </div>
                            <div className="l-col l-col--auto u-small-hide u-spacing-right-l u-padding-l u-padding-top-l u-padding-left-s">
                                <a href="#" onClick={() => this.props.onRemove()}>
                                    {this.props.descriptions.deleteLabel}
                                </a>
                            </div>
                        </div> : null}
                    </div>
                </div>
            </div>
        )
    }
}

const oplLayoutFixerModule = () => ({
    path: "core/modules/layout-fixer",
    conditions: "element:{was seen}",
    options: {
        selectors: [
            ".js-layout-fixer-group-1"
        ]
    }
});

const jsModules = () => ([
    oplLayoutFixerModule()
]);

const mapDispatchToProps = dispatch => ({
    redirectOnCustomerSearch: () => dispatch(redirectOnCustomerSearch()),
    clearCartAndRedirect: (id, url) => dispatch(clearCartAndRedirect(id, url))
});

const mapStateToProps = state => ({
});

CommonPropositionEntry.propTypes = {
    mainEntries: PropTypes.array,
    subEntries: PropTypes.array,
    offerPrice: PropTypes.object,
    loyaltyLength: PropTypes.number,
    bundleNo: PropTypes.number,
    entryNo: PropTypes.number,
    onRemove: PropTypes.func,
    onDetails: PropTypes.func,
    onChange: PropTypes.func,
    descriptions: PropTypes.object,
    overrideDetailsPartBy: PropTypes.object,
    showButtons: PropTypes.bool
}

CommonPropositionEntry.defaultProps = {
    showButtons:true,
    subEntries : []
};


export default connect(mapStateToProps, mapDispatchToProps)(CommonPropositionEntry);